.topbar__container{
    height: 50px;
    width: 100%;
    background: #1877f2;
    display: flex;
    align-items: center;
    position: sticky;
    top: 0;
    z-index: 999;
}

.topbarLeft{
    flex: 3;
}

.logo{
    font-size: 24px;
    font-weight: bold ;
    margin-left: 20px;
    color: white ;
    cursor: pointer;
}

.searchbar{
    width: 100%;
    height: 30px;
    background: white ;
    border-radius: 30px;
    display: flex;
    align-items: center;
   
    
}
.searchIcon{
    font-size: 20px important;
    margin-left: 10px;
}

.searchInput{
    border: none ;
    width: 70%;
}

.searchInput:focus{
    outline: none;
}

.topbarCenter{
    flex: 5;
}

.topbarRight{
    flex: 4;
    display: flex;
    align-items: center;
    justify-content: space-around;
    color: white;
}



.topbarLink{
    margin-right: 10px;
    font-size: 14px;
    cursor: pointer;
}

.topbarIcons{
    display: flex;
}

.topbarIconItem{
    margin-right: 15px;
    cursor: pointer;
    position: relative;
}

.topbarIconBadge{
    width: 15px;
    height: 15px;
    background: red;
    border-radius: 50%;
    color: white ;
    position: absolute;
    top: -5px;
    right: -5px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 12px;
}


.topbarImg{
    width: 32px;
    height: 32px;
    border-radius: 50%;
    object-fit: cover;
}